<template>
  <div>
    <PublicHeader title="查看详情" />
    <div class="question font16 cr333">
      {{ index + 1 }}. {{ question }}
    </div>
    <div class="answer font14 cr666">
      答：{{ answer }}
    </div>
  </div>
</template>
<script>
  import PublicHeader from '../../../components/PublicHeader';

  export default {
    data () {
      return {
        index: 0,
        question: '',
        answer: '',
        qa: [
          {
            question: '付费模拟题有什么特质？',
            answer: '每套付费模拟题的题型、分数和题目数，和正式竞赛保持一致，可针对性联系，提高竞赛成绩。'
          },
          {
            question: '模拟题购买后能否退费？',
            answer: '每套付费模拟题的题型、分数和题目数，和正式竞赛保持一致，可针对性联系，提高竞赛成绩。'
          },
          {
            question: '报名支付后能够退费？退费规则是什么？',
            answer: '每套付费模拟题的题型、分数和题目数，和正式竞赛保持一致，可针对性联系，提高竞赛成绩。'
          },
        ]
      }
    },
    components: { PublicHeader },
    mounted () {
      this.index = this.$route.params[0] - 0;
      this.question = this.qa[this.index].question;
      this.answer = this.qa[this.index].answer;
    }
  }
</script>
<style scoped>
  .question, .answer {
    padding: .4rem;
  }
</style>


