<template>
  <div>
    <div class="content" v-show="condition"></div>
  </div>
</template>
<script>
  export default {
    props: ['condition']
  }
</script>
<style scoped>
  .content {
    display: flex;
  }
  .content:after {
    content: '';
    position: absolute;
    width: .185rem;
    height: .185rem;
    border-radius: .185rem;
    background-color: red;
    margin-top: -.25rem;
    margin-left: -.15rem;
  }
</style>


