<template>
  <div class="wrap df">
    <!-- paid exercises -->
    <PublicHeader title="练习历史" />
    <div class="container f1" v-show="exercises.history.length != 0">
      <PublicItems
        ref="items"
        :payload="exercises.history">
        <div
          slot="header"
          class="loadmore font12 tc cr666"
          ref="loadmore">
          {{ notice }}
        </div>
        <!-- unselected item -->
        <div slot-scope="props" class="item df center link" slot="item">
          <div class="f1">
            <div class="font16 cr333">{{ props.item.exercisesName }}</div>
            <div class="font12 cr999">{{ props.item.create_time | formatTime }}</div>
          </div>
          <div class="df center font14">
            <span class="cr999">{{ props.item.score }}分</span><i class="fa-icon fa-arrow-right"></i>
          </div>
        </div>
      </PublicItems>
      <div class="font14 tc cr999 notice">已经到底啦~</div>
    </div>
    <div class="container f1 df center tc" v-show="exercises.history.length == 0">
      <div class="empty"></div>
    </div>
  </div>
</template>
<script>
  import { Func } from '../../../vendor/lib';
  import PublicHeader from '../../../components/PublicHeader';
  import PublicItems from '../../../components/PublicItems';
  import PublicNotice from '../../../components/PublicNotice';

  export default {
    data () {
      return {
        exercises: {
          history: [],
        },
        notice: '再滑，再滑就加载给你看~0~(骗你的',
      }
    },
    filters: {
      formatTime (str) {
        return Func.formatTime(str, 'YYYY年MM月DD日 hh:mm');
      }
    },
    components: { PublicHeader, PublicItems, PublicNotice },
    mounted () {
      let id = 0;

      // this.$bubble('selectexerciseshistory');

      this.$refs.items.$el.style.marginTop = `${-this.$refs.loadmore.offsetHeight}px`;

      this.$processor('oldDrag')
      .then((processor) => {
        processor.mount(['move', 'end']);
      })
      .move((res) => {
        if (res.move > 1) {
          this.notice = '放，放开人家啦，讨厌～';
        }
        if (res.move < 1) {
          this.notice = '再滑，再滑就加载给你看~0~(骗你的';
        }
      })
      .end((res) => {
        if (res.move > .9 && res.move < 1.1) {
          this.notice = '刷呀，刷呀，好累呀～';
          id++;
          setTimeout((rafId) => {
            if (rafId == id) {
              this.notice = '加载失败惹QWQ';
            }
          }, 2000, id);
          setTimeout((rafId) => {
            if (rafId == id) {
              res.continue();
            }
          }, 3000, id)
          res.pause();
          res.end();
        }
      })
    }
  }
</script>
<style scoped>
  .item {
    padding: .4rem;
    border-bottom: 1px solid #ececec;
  }
  .item:active {
    background-color: #eee;
  }
  .item .fa-icon {
    width: .16rem;
    height: .32rem;
    margin-left: .28rem;
    background-size: .16rem .32rem;
  }
  .notice {
    margin-top: 2rem;
  }
  .empty {
    background-image: url('../../../assets/img/history_empty.png');
    width: 3.8rem;
    height: 5.3rem;
    margin-top: -3rem;
    background-size: 3.8rem;
    background-repeat: no-repeat;
  }
</style>


