<template>
  <div class="wrap df" :style="toast.length == 0 ? '' : style.wrap">
    <!-- user toast -->
    <PublicHeader :className="toast.length == 0 ? '' : style.navbar" title="我的消息" />
    <div class="container f1" v-show="toast.length != 0">
      <PublicItems
        ref="items"
        :payload="toast">
        <div
          slot="header"
          class="loadmore font12 tc cr999"
          ref="loadmore">
          {{ notice }}
        </div>
        <!-- unselected item -->
        <div slot-scope="props" class="item link" slot="item">
          <div class="tc date font14 cr999">10月17日 17:40</div>
          <div class="df center">
            <div class="avatar"></div>
            <div class="df center font14 cr333 content">
              此处应有一些乱七八糟的推送，我去年买了个登蜀道难，难于上青天，蚕丛及鱼凫，开国何茫然
            </div>
          </div>
        </div>
      </PublicItems>
    </div>
    <div class="container f1 df center tc" v-show="toast.length == 0">
      <div class="empty"></div>
    </div>
  </div>
</template>
<script>
  import { Func } from '../../../vendor/lib';
  import PublicHeader from '../../../components/PublicHeader';
  import PublicItems from '../../../components/PublicItems';
  import PublicNotice from '../../../components/PublicNotice';

  export default {
    data () {
      return {
        notice: '再滑，再滑就加载给你看~0~(骗你的',
        toast: [0, 1, 2, 3, 4, 5, 6],
        style: {
          navbar: {
            boxShadow: '0 .1rem .24rem #eaeaea',
          },
          wrap: {
            backgroundColor: '#eee',
          }
        }
      }
    },
    filters: {
      formatTime (str) {
        return Func.formatTime(str, 'YYYY年MM月DD日 hh:mm');
      }
    },
    components: { PublicHeader, PublicItems, PublicNotice },
    mounted () {
      let id = 0;
      this.$refs.items.$el.style.marginTop = `${-this.$refs.loadmore.offsetHeight}px`;

      this.$processor('oldDrag')
      .then((processor) => {
        processor.mount(['move', 'end']);
      })
      .move((res) => {
        if (res.move > 1) {
          this.notice = '放，放开人家啦，讨厌～';
        }
        if (res.move < 1) {
          this.notice = '再滑，再滑就加载给你看~0~(骗你的';
        }
      })
      .end((res) => {
        if (res.move > .9 && res.move < 1.1) {
          this.notice = '刷呀，刷呀，好累呀～';
          id++;
          setTimeout((rafId) => {
            if (rafId == id) {
              this.notice = '加载失败惹QWQ';
            }
          }, 2000, id);
          setTimeout((rafId) => {
            if (rafId == id) {
              res.continue();
            }
          }, 3000, id)
          res.pause();
          res.end();
        }
      })
    }
  }
</script>
<style scoped>
  .container {
    padding-bottom: 1.8rem;
  }
  .empty {
    background-image: url('../../../assets/img/toast_empty.png');
    width: 3.8rem;
    height: 5.3rem;
    margin-top: -1rem;
    background-size: 3.8rem;
    background-repeat: no-repeat;
  }
  .item {
    padding: .32rem;
    padding-left: .4rem;
  }
  .item .content {
    background-color: #fff;
    width: 6rem;
    padding: .5rem;
    border-radius: .5rem;
    box-shadow: 4px 4px 24px #cfcfcf;
  }
  .item .date {
    padding: .3rem;
  }
  .item .avatar {
    margin-right: .5rem;
    background-image: url('../../../assets/img/toast_avatar.png');
    background-size: 1.2rem;
    width: 1.2rem;
    height: 1.2rem;
  }
  .item .content:before {
    content: '';
    position: relative;
    margin-left: -.4rem;
    left: -.4rem;
    top: -.2rem;
    border-top: .4rem solid transparent;
    border-right: .4rem solid #fff;
  }
</style>


