<template>
  <div class="wrap df">
    <PublicHeader title="在线客服" />
    <div class="container f1">
      <div class="title font13 cr999">常见问题</div>
        <PublicItems :payload="questions">
        <div slot-scope="props" class="cr666 font14 item" slot="item" @click="redirect(props.index)">
          {{ props.index + 1 }}. {{ props.item }}
        </div>
      </PublicItems>
    </div>
    <div class="df font16 center public-footer">
      <div class="f1 tc df center">
        <i class="fa-icon fa-headset"></i>
        <span class="btn">在线客服</span>
      </div>
      <div class="separate cr999"></div>
      <div class="f1 tc df center">
        <i class="fa-icon fa-phone"></i>
        <span class="btn">客服热线</span>
      </div>
    </div>
  </div>
</template>
<script>
  import PublicHeader from '../../../components/PublicHeader'
  import PublicItems from '../../../components/PublicItems'

  export default {
    data () {
      return {
        questions: [
          '付费模拟题有什么特质？',
          '模拟题购买后能否退费？',
          '报名支付后能够退费？退费规则是什么？',
        ]
      }
    },
    methods: {
      redirect(index) {
        this.$request('redirect', {
          path: `/custom/service/${index}`,
        })
      }
    },
    components: { PublicHeader, PublicItems },
  }
</script>
<style scoped>
  .item {
    padding: .48rem 0;
    padding-left: .4rem;
    border-bottom: 1px solid #ececec;
  }
  .item:active {
    background-color: #eee;
  }
  .title {
    padding-bottom: .3rem;
    padding-top: .56rem;
    padding-left: .4rem;
  }

  .public-footer {
    box-shadow: 0 -0.1rem 0.24rem #f6f6f6;
  }
  .public-footer .fa-icon {
    width: .45rem;
    height: .45rem;
    background-size: .45rem;
  }
  .public-footer span {
    height: 1.32rem;
  }
</style>


