<template>
  <div>
    <!-- paid exercises -->
    <PublicHeader title="模拟练习册">
      <div slot="entry" class="df center">
        <span class="crSkyBlue df center" @click="redirectToHistory">练习历史</span>
      </div>    
    </PublicHeader>
    <div class="tag">
      <div class="content font18">
        已购模拟
      </div>
    </div>
    <PublicItems :payload="exercises.paid">
      <!-- unselected item -->
      <div slot-scope="props" class="item df center link" slot="item">
        <div class="f1">
          <div class="font16 cr333 df center">
            {{ props.item.exercisesName }}
            <PublicNotice :condition="props.item.exercisesCount == 0" />
          </div>
          <div class="font12 cr999">{{ props.item.exercisesNumber }}道题</div>
        </div>
        <div class="df center font14">
          <span class="cr999">已练{{ props.item.exercisesCount }}次</span><i class="fa-icon fa-arrow-right"></i>
        </div>
      </div>
    </PublicItems>
    <!-- free exercises -->
    <div class="tag">
      <div class="content font18">
        免费模拟
      </div>
    </div>
    <PublicItems :payload="exercises.free">
      <!-- unselected item -->
      <div slot-scope="props" class="item df center link" slot="item">
        <div class="f1">
          <div class="font16 cr333 df center">
            {{ props.item.exercisesName }}
            <PublicNotice :condition="props.item.exercisesCount == 0" />
          </div>
          <div class="font12 cr999">{{ props.item.exercisesNumber }}道题</div>
        </div>
        <div class="df center font14">
          <span class="cr999">已练{{ props.item.exercisesCount }}次</span><i class="fa-icon fa-arrow-right"></i>
        </div>
      </div>
    </PublicItems>
  </div>
</template>
<script>
  import PublicHeader from '../../../components/PublicHeader';
  import PublicItems from '../../../components/PublicItems';
  import PublicNotice from '../../../components/PublicNotice';

  export default {
    data () {
      return {
        exercises: {}
      }
    },
    components: { PublicHeader, PublicItems, PublicNotice },
    methods: {
      redirectToHistory () {
        this.$request('redirect', {
          path: '/exercises/history',
        })
      }
    },
    mounted () {
      this.$bubble('selectexercisesrecord');
    }
  }
</script>
<style scoped>
  .item {
    padding: .4rem;
    border-bottom: 1px solid #ececec;
  }
  .item:active {
    background-color: #eee;
  }
  .item .fa-icon {
    width: .16rem;
    height: .32rem;
    margin-left: .28rem;
    background-size: .16rem .32rem;
  }
  .tag {
    padding: .4rem;
    padding-left: 0;
  }
  .tag .content {
    border-left: .08rem solid #69b8ff;
    padding-left: .32rem;
  }
</style>


