<template>
  <div class="public-header">
    <div class="contain df center cr333" :style="className">
      <div @click="goBack" class="link placeholder df center">
        <div class="df center"><i class="fa-icon fa-arrow-left"></i></div>
      </div>
      <div class="title font18">{{ title }}</div>
      <div class="link placeholder df center font16 entry">
        <div class="df center"><slot name="entry"></slot></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ['title', 'className'],
  methods: {
    goBack () {
      this.$bubble('goback');
    }
  },
}
</script>
<style scoped>
  .public-header {
    position: relative;
    z-index: 1;
    background-color: #fff;
  }
  .contain {
    height: 1.17rem;
    justify-content: space-between;
    box-shadow: 0 .1rem .24rem #f6f6f6;
  }
  .fa-arrow-left {
    width: .25rem;
    height: .44rem;
    background-size: .25rem .44rem;
    margin-left: .4rem;
  }
  .entry {
    flex-direction: row-reverse;
  }
  .entry > * {
    padding-right: .4rem;
  }
  .placeholder {
    width: 3rem;
  }
</style>


